import * as React from "react";
import {Layout, Card, message,
	Select, List, Avatar, Button,
	Skeleton, Switch,
	Drawer, Form,
	Input,
	DatePicker
} from 'antd';
import moment from 'moment';
import zhCN from 'antd/es/date-picker/locale/zh_CN';
import LC from "leancloud-storage";
import Zmage from "react-zmage";
import Http from "../util/http";
import {IMClass} from "../Classes/IMClass";
const { Option } = Select;
const count = 3;
const key = 'getReports';
export default class Report extends React.Component{
	constructor(props) {
		super(props);
		this.state={
			initLoading: true,
			loading: false,
			isLoadingMore: true,
			data: [],
			list: [],
			drawerTitle: '',
			handle_type: undefined,
		}
		this.selItem = null;
		this.msgObject = 0; //01
		this.createdAt = new Date();
		this.gag_deadline = null;
	}
	componentDidMount() {
		this.getData(res => {
			this.setState({
				initLoading: false,
				data: res,
				list: res,
			});
		});
	}
	componentWillUnmount() {}
	handleChange = value=> {
		console.log(`selected ${value}`);
		this.setState({
			handle_type: value,
		});
	}
	getData = callback => {
		let query = new LC.Query('Report');
		query.limit(10);
		query.include('plaintiff');
		query.include('defendant');
		query.include('story');
		query.lessThan('createdAt', this.createdAt);
		query.descending("createdAt");
		query.find().then(r => {
			if(r.length > 0){
				this.createdAt = r[r.length - 1]?.createdAt;
			} else {
				this.setState({
					isLoadingMore: false,
				});
			}
			this.setUsers(r, callback);
			setTimeout(() => {
				message.success({ content: '', key, duration: 2 });
			}, 1000);
		});
	};
	onLoadMore = () => {
		if(!this.state?.isLoadingMore){return;}
		this.setState({
			loading: true,
			list: this.state.data.concat([...new Array(count)].map(() => ({ loading: true, name: {} }))),
		});
		this.getData(res => {
			console.log('', res);
			const data = this.state.data.concat(res);
			this.setState(
				{
					data,
					list: data,
					loading: false,
				},
				() => {window.dispatchEvent(new Event('resize'));},
			);
		});
	};
	showDrawer = ({title}) => {
		this.setState({
			visible: true,
			drawerTitle: title,
			handle_type: undefined,
		});
	};
	onClose = () => {
		this.setState({
			visible: false,
		});
	};
	onSubmit = () =>{
		const {handle_type, handle_description, list} = this.state;
		const report = LC.Object.createWithoutData("Report", this.selItem?.id);
		if(this.msgObject === 1){
			IMClass?.getServiceConversationAndSendMsg(()=>{
				message.success('');
				report.set("isFeedbackPlaintiff",true);
				report.save().then(res=>{
					console.log('');
					let index = list.findIndex((item => item.id === this.selItem?.id));
					if(index !== -1){
						list[index].report.isFeedbackPlaintiff = true;
					}
					this.forceUpdate();
					this.onClose();
				});
			}, this.selItem?.plaintiffId, this.state?.handle_description);
			return;
		}
		if(!handle_description && handle_type !== 2){
			message.warning('');
			return;
		}
		report.set("isHandle",true);
		report.set("handle_type",handle_type);
		report.set("handle_description", handle_type !== 2 ? handle_description : '');
		report.save().then(res=>{
			if(handle_type === 1){
				//
				if(!this.gag_deadline){
					message.error('');
					return;
				}
				Http.put(
					`/users/${this.selItem?.defendantId ?? ''}`,
					{
						gag_deadline: this.gag_deadline,
						numberOfViolations: (this.selItem?.defendant?.numberOfViolations ?? 0) + 1,
					},
					true).then(res=>{
					message.success('');
					let index = list.findIndex((item => item.id === this.selItem?.id));
					if(index !== -1){
						list[index].report.isHandle = true;
						list[index].report.handle_type = handle_type;
						list[index].report.handle_description = handle_description;
					}
					this.forceUpdate();
				}).catch(err=>{
					message.error('');
					console.log('', err);
				})
			}
			if(handle_type === 2){
				//
				Http.put(
					`/users/${this.selItem?.defendantId ?? ''}`,
					{
						isLocked: true,
						numberOfViolations: (this.selItem?.defendant?.numberOfViolations ?? 0) + 1,
					},
					true).then(res=>{
					message.success('');
					let index = list.findIndex((item => item.id === this.selItem?.id));
					if(index !== -1){
						list[index].report.isHandle = true;
						list[index].report.handle_type = handle_type;
					}
					this.forceUpdate();
				}).catch(err=>{
					message.error('');
					console.log('', err);
				})
			}
			if(handle_type === 0){
				message.success('');
				let index = list.findIndex((item => item.id === this.selItem?.id));
				if(index !== -1){
					list[index].report.handle_description = handle_description;
					list[index].report.isHandle = true;
					list[index].report.handle_type = handle_type;
				}
				this.forceUpdate();
			}
			this.onClose();
			IMClass?.getServiceConversationAndSendMsg(()=>{
				message.success('');
			}, this.selItem?.defendantId, this.state?.handle_description, handle_type);
		});
	}
	render() {
		const { initLoading, loading, list, handle_type, isLoadingMore } = this.state;
		const loadMore =
			!initLoading && !loading ? (
				<div
					style={{
						textAlign: 'center',
						marginTop: 22,
						height: 88,
						lineHeight: '32px',
					}}
				>
					<Button onClick={this.onLoadMore}>{isLoadingMore ? '' : ''}</Button>
				</div>
			) : null;
		return <Layout style={{minHeight: '100vh'}}>
			<div style={{
				margin: 12,
				borderRadius: 8,
				boxShadow: '0px 2px 8px #f2f2f2',
				padding: 12,
				backgroundColor: 'white'
			}}>
				<List
					loading={initLoading}
					style={{width: 'auto'}}
					itemLayout="vertical"
					loadMore={loadMore}
					dataSource={list}
					header={
						<div style={{
							display: 'flex',
							alignItems: 'center',
							justifyContent: 'flex-start',
							fontWeight: 'bold',
						}}>
							
						</div>
					}
					split={false}
					renderItem={item => (
						<List.Item
							key={item.id}
							actions={[]}
							// extra={}
						>
							<Skeleton avatar title={false} loading={item.loading} active>
								<List.Item.Meta
									avatar={
										<Avatar src={item?.plaintiff?.avatar?.attributes?.url} />
									}
									title={item?.plaintiff?.nickname ?? ''}
									description={item?.plaintiff?.introduce ?? ''}
								/>
							</Skeleton>
							<div>
								<Card style={{minWidth: 500}}>
									<div style={{display: 'flex'}}>
									<div></div>
									<List.Item.Meta
										avatar={
											<Avatar src={item?.defendant?.avatar?.attributes?.url} />
										}
										title={item?.defendant?.nickname ?? ''}
										description={
											<div>
												<div>
													{'ID'+item?.plaintiffId ?? ''}
												</div>
												<div style={{marginTop: 6}}>
													{''+item?.plaintiff?.mobilePhoneNumber ?? ''}
												</div>
												<div style={{marginTop: 6}}>
													{''+item?.plaintiff?.numberOfViolations ?? '0'}
												</div>
												<div style={{marginTop: 6}}>
													{''+ (item?.plaintiff?.gag_deadline ?? '')}
												</div>
											</div>
										}
									/>
								</div>
									<div style={{}}>
										<span style={{color: 'darkgray'}}>{!item?.storyId ? '' : ''}</span>
									</div>
									<br/>
									<div style={{}}>
										<span style={{color: 'darkgray'}}>{JSON.parse(item?.report?.type ?? '{"title": ""}')?.title ?? ''}</span>
									</div>
									<br/>
									<div>
										<span style={{color: 'darkgray'}}>{item?.report?.reason}</span>
									</div>
									<br/>
									<div>
										<span style={{color: 'darkgray'}}>{item?.report?.isHandle ? '' : ''}</span>
									</div>
									<br/>
									<div style={{}}>
										
										<div>
											{
												item?.images?.map((img=><Zmage
													style={{marginTop: 12, marginRight: 12}}
													width={256}
													alt="auth"
													src={img?.uri}
													key={img?.id}
													
												/>))
											}
										</div>
									</div>
									<br/>
									<div>
										<Button
											type="dashed"
											style={{minWidth: 100, marginTop: 12}}
											onClick={()=>{
												this.msgObject = 0;
												this.selItem = item;
												this.showDrawer({title: ''});
											}}
											disabled={item?.report?.isHandle}>
											{!item?.report?.isHandle ? '' : ''}
										</Button>
										<Button
											type="dashed"
											style={{minWidth: 100, marginLeft: 12}}
											disabled={!item?.report?.isHandle || item?.report?.isFeedbackPlaintiff}
											onClick={()=>{
												this.msgObject = 1;
												this.selItem = item;
												this.showDrawer({title: ''});
											}}>
											{item?.report?.isFeedbackPlaintiff ? '' : ''}
										</Button>
									</div>
									{
										item?.report?.isHandle &&
										<div>
											<br/>
											<Card  style={{color: "gray"}}>
												<div>
													{
														'' + (item?.report?.handle_type == 0 ? '' :
															item?.report?.handle_type == 1 ? '' : '')
													}
												</div>
												<div style={{marginTop: 6}}>
													{''+item?.report?.handle_description ?? ''}
												</div>
											</Card>
										</div>
									}
								</Card>
								<br/>
								{
									item?.storyId && <div>
										<Card>
											<div style={{}}>
												
												<Card>
													<div>
														ID<span style={{color: 'darkgray'}}>{item?.storyId ?? ''}</span>
													</div>
													<div>
														<span style={{color: 'darkgray'}}>{item?.story?.theme ?? ''}</span>
													</div>
													<div>
														<span style={{color: 'darkgray'}}>{item?.story?.content ?? ''}</span>
													</div>
													
													<div>
														{
															item?.story?.images?.map((img=><Zmage
																style={{marginTop: 12, marginRight: 12}}
																width={68}
																alt="auth"
																src={img?.uri}
															/>))
														}
													</div>
													<br/>
													<div style={{color: 'gray'}}>
														
														<Switch
															style={{}}
															checked={item?.story?.isShielded ?? false}
															onClick={(isLocked)=>{
																console.log('', isLocked);
																this.shieldedStory(item, isLocked);
															}}
														/>
													</div>
												</Card>
											</div>
										</Card>
										<br/>
									</div>
								}
							</div>
							<div style={{
								display: 'flex',
								height: 6,
								backgroundColor: '#f2f2f2'
							}}/>
						</List.Item>
					)}
				/>
				<Drawer
					closable={false}
					title={this.state?.drawerTitle ?? ''}
					width={558}
					onClose={this.onClose}
					visible={this.state.visible}
					bodyStyle={{ paddingBottom: 80 }}>
					<Form layout="vertical" hideRequiredMark>
						{
							this.msgObject === 0 &&
							<Form.Item label="">
								<Select
									placeholder="..."
									value={handle_type}
									onChange={this.handleChange}>
									<Option value={0}></Option>
									<Option value={1}></Option>
									<Option value={2}></Option>
								</Select>
							</Form.Item>
						}
						{
							this.msgObject === 0 && handle_type === 1 &&
							<Form.Item label="">
								<DatePicker
									style={{ width: '100%' }}
									locale={zhCN}
									getPopupContainer={trigger =>trigger.parentNode}
									onChange={(date, dateString)=> {
										this.gag_deadline = {
											"__type": "Date",
											"iso": moment(dateString).toISOString() //restAPI
										};
									}}
								/>
							</Form.Item>
						}
						{
							handle_type !== 2 &&
							<Form.Item label="">
								<Input.TextArea
									rows={4}
									onChange={(e)=>{
										const { value } = e.target;
										this.setState({
											handle_description: value,
										});
									}}
									placeholder={this.msgObject === 0 ? "..."
										: '...'}/>
							</Form.Item>
						}
					</Form>
					<div
						style={{
							position: 'absolute',
							right: 0,
							bottom: 0,
							width: '100%',
							borderTop: '1px solid #e9e9e9',
							padding: '10px 16px',
							background: '#fff',
							textAlign: 'right',
						}}
					>
						<Button onClick={this.onClose} style={{ marginRight: 8 }}>
							
						</Button>
						<Button onClick={this.onSubmit} type="primary">
							
						</Button>
					</div>
				</Drawer>
			</div>
		</Layout>
	}
	setUsers(r, callback){
		let reports = [];
		for(let i = 0; i<r.length; i++){
			let report = r[i]?.attributes;
			let report_id = r[i]?.id;
			let defendant = report?.defendant?.attributes ?? {};//
			let plaintiff = report?.plaintiff?.attributes ?? {};//
			let report_story = report?.story?.attributes;
			let report_obj = {
				id: report_id,
				defendant: defendant,
				plaintiff: plaintiff,
				defendantId: report?.defendant?.id,
				plaintiffId: report?.plaintiff?.id,
				report: report,
				images: report?.images ?? [],
				storyId: report?.story?.id,
				story: report_story,
			}
			reports.push(report_obj);
		}
		callback(reports);
	}
	//
	shieldedStory(item, isLocked){
		const story = LC.Object.createWithoutData("Story", item?.storyId);
		story.set("isShielded",isLocked);
		story.save().then(s=>{
			item.story.isShielded = isLocked;
			message.success(isLocked ? '' : '');
			this.forceUpdate();
		});
	}
}
