import * as React from "react";
import {Layout, Card, message, Select, List, Avatar, Button, Skeleton, Form, Input, Drawer} from 'antd';
import LC from "leancloud-storage";
import Zmage from "react-zmage";
import {IMClass} from "../Classes/IMClass";
const count = 3;
const key = 'getfeedbacks';
export default class Feedback extends React.Component{
	constructor(props) {
		super(props);
		this.state={
			initLoading: true,
			loading: false,
			isLoadingMore: true,
			data: [],
			list: [],
			visible: '',
		}
		this.createdAt = new Date();
		this.selItem = null;
	}
	componentDidMount() {
		this.getData(res => {
			this.setState({
				initLoading: false,
				data: res,
				list: res,
			});
		});
	}
	componentWillUnmount() {}
	showDrawer = () => {
		this.setState({
			visible: true,
		});
	};
	onClose = (handle_type) => {
		this.setState({
			visible: false,
		});
	};
	handleChange = value=> {
		console.log(`selected ${value}`);
	}
	getData = callback => {
		let query = new LC.Query('Feedback');
		query.include('user');
		query.descending("createdAt");
		query.lessThan('createdAt', this.createdAt);
		query.limit(10);
		query.find().then(r => {
			setTimeout(() => {
				message.success({ content: '', key, duration: 2 });
			}, 1000);
			if(r.length > 0){
				this.createdAt = r[r.length - 1]?.createdAt;
			} else {
				this.setState({
					isLoadingMore: false,
				});
			}
			this.setUsers(r, callback);
		});
	};
	
	onLoadMore = () => {
		if(!this.state?.isLoadingMore){return;}
		this.setState({
			loading: true,
			list: this.state.data.concat([...new Array(count)].map(() => ({ loading: true, name: {} }))),
		});
		this.getData(res => {
			const data = this.state.data.concat(res);
			this.setState(
				{
					data,
					list: data,
					loading: false,
				},
				() => {
					// Resetting window's offsetTop so as to display react-virtualized demo underfloor.
					// In real scene, you can using public method of react-virtualized:
					// https://stackoverflow.com/questions/46700726/how-to-use-public-method-updateposition-of-react-virtualized
					window.dispatchEvent(new Event('resize'));
				},
			);
		});
	};
	render() {
		const { initLoading, loading, list, isLoadingMore } = this.state;
		const loadMore =
			!initLoading && !loading ? (
				<div
					style={{
						textAlign: 'center',
						marginTop: 22,
						height: 88,
						lineHeight: '32px',
					}}
				>
					<Button onClick={this.onLoadMore}>{isLoadingMore ? '' : ''}</Button>
				</div>
			) : null;
		return <Layout style={{minHeight: '100vh'}}>
			<div style={{
				margin: 12,
				borderRadius: 8,
				boxShadow: '0px 2px 8px #f2f2f2',
				padding: 12,
				backgroundColor: 'white'
			}}>
				<List
					loading={initLoading}
					style={{width: 'auto'}}
					itemLayout="vertical"
					loadMore={loadMore}
					dataSource={list}
					header={
						<div style={{
							display: 'flex',
							alignItems: 'center',
							justifyContent: 'flex-start',
							fontWeight: 'bold',
						}}>
							
						</div>
					}
					renderItem={item => (
						<List.Item
							actions={[]}
							// extra={}
						>
							<Skeleton avatar title={false} loading={item.loading} active>
								<List.Item.Meta
									avatar={
										<Avatar src={item?.user?.avatar?.attributes?.url} />
									}
									title={item?.user?.nickname ?? ''}
									description={item?.user?.introduce ?? ''}
								/>
							</Skeleton>
							<div>
								<Card style={{}}>
									<div style={{}}>
										<span style={{color: 'darkgray'}}>{JSON.parse(item?.feedback?.type ?? '{"title": ""}')?.title ?? ''}</span>
									</div>
									<br/>
									<div>
									  <span style={{color: 'darkgray'}}>{item?.feedback?.content}</span>
									</div>
									<br/>
									<div>
										<span style={{color: 'darkgray'}}>{item?.feedback?.isHandle ? '' : ''}</span>
									</div>
									<br/>
									<div style={{}}>
										
										<div>
											{
												item?.images?.map((img=><Zmage
													style={{marginTop: 12, marginRight: 12}}
													width={256}
													alt="auth"
													src={img?.uri}
												/>))
											}
										</div>
									</div>
									<br/>
									<Button
										type="dashed" style={{minWidth: 100}}
										onClick={()=>{
											this.selItem = item;
											this.showDrawer();
										}}
										disabled={item?.feedback?.isHandle}>
										{item?.feedback?.isHandle ? '' : ''}</Button>
									{
										item?.feedback?.isHandle &&
										<div>
											<br/>
											<div>
												<span style={{color: 'darkgray'}}>{item?.feedback?.return_content}</span>
											</div>
										</div>
									}
								</Card>
							</div>
						</List.Item>
					)}
				/>
				<Drawer
					closable={false}
					title={''}
					width={558}
					onClose={this.onClose}
					visible={this.state.visible}
					bodyStyle={{ paddingBottom: 80 }}>
					<Form layout="vertical" hideRequiredMark>
						<Form.Item label="">
							<Input.TextArea
								rows={4}
								onChange={(e)=>{
									const { value } = e.target;
									this.setState({
										handle_description: value,
									});
								}}
								placeholder={'...'}/>
						</Form.Item>
					</Form>
					<div
						style={{
							position: 'absolute',
							right: 0,
							bottom: 0,
							width: '100%',
							borderTop: '1px solid #e9e9e9',
							padding: '10px 16px',
							background: '#fff',
							textAlign: 'right',
						}}
					>
						<Button onClick={this.onClose} style={{ marginRight: 8 }}>
							
						</Button>
						<Button onClick={this.onSubmit} type="primary">
							
						</Button>
					</div>
				</Drawer>
			</div>
		</Layout>
	}
	onSubmit = () =>{
		const {handle_description, list} = this.state;
		const feedback = LC.Object.createWithoutData("Feedback", this.selItem?.id);
		IMClass?.getServiceConversationAndSendMsg(()=>{
			message.success('');
			feedback.set("isHandle", true);
			feedback.set("return_content", handle_description);
			feedback.save().then(res=>{
				console.log('');
				let index = list.findIndex((item => item.id === this.selItem?.id));
				if(index !== -1){
					list[index].feedback.isHandle = true;
					list[index].feedback.return_content = handle_description;
				}
				this.forceUpdate();
				this.onClose();
			});
		}, this.selItem?.uid, this.state?.handle_description);
	}
	setUsers(r, callback){
		let feedbacks = [];
		for(let i = 0; i<r.length; i++){
			let feedback = r[i]?.attributes;
			let feedback_id = r[i]?.id;
			let user = feedback?.user?.attributes ?? {};
			let feedback_obj = {
				id: feedback_id,
				uid: feedback?.user?.id,
				user: user,
				feedback: feedback,
				images: feedback?.images ?? [],
			}
			feedbacks.push(feedback_obj);
		}
		callback(feedbacks);
	}
}
